import { useState } from "react";
import { BASE_URL, URLS } from "../../pages/url";
import { Error } from "../../components/error/Error";

export const DailyAppend = () => {
  const [formData, setFormData] = useState({
    cashIn: "",
    cashOut: "",
    pending: "",
    bill: "",
    category: "",
    desc: "",
  });

  const [error, setError] = useState(null);

  const handleOnChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    setFormData({
      ...formData,
      [id]: value,
    });
  };

  const handleOnSubmit = (e) => {
    e.preventDefault();
    const data = new FormData();

    data.append("date", Math.floor(Date.now() / 1000));
    data.append("cashIn", formData["cashIn"]);
    data.append("cashOut", formData["cashOut"]);
    data.append("pending", formData["pending"]);
    data.append("bill", formData["bill"]);
    data.append("category", formData["category"]);
    data.append("desc", formData["desc"]);

    const URL = BASE_URL + URLS.royaladd;

    fetch(URL, {
      method: "post",
      body: data,
    })
      .then((response) => {
        if (response.ok) return response.json();
        else {
          throw new Error("Error adding Entery to Royal Daily");
        }
      })
      .then((data) => {
        if (data.msg == "SUCSESS") alert(data.msg);
        else setError(data.msg);
      })
      .catch((error) => {
        console.error("Error ", error);
        setError(error);
      });
    document.getElementById("royal-daily").reset();
  };

  return (
    <div>
      {error ? (
        <Error />
      ) : (
        <div className="m-5 p-3">
          <form
            className="w-full max-w-lg"
            id="royal-daily"
            onSubmit={handleOnSubmit}
          >
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  CashIn
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="cashIn"
                  onInput={handleOnChange}
                  type="number"
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  CashOut
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="number"
                  id="cashOut"
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Pending
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="pending"
                  type="number"
                  onInput={handleOnChange}
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Bill No.
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  id="bill"
                  type="number"
                  placeholder="Bill No"
                />
              </div>

              <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2"
                  for="grid-state"
                >
                  Category
                </label>
                <div class="relative">
                  <select
                    class="block appearance-none w-full bg-gray-200 border border-blue-500 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                    onChange={handleOnChange}
                    id="category"
                  >
                    <option>Select</option>
                    <option>Bed</option>
                    <option>Mattres</option>
                    <option>Pillow</option>
                    <option>Razai set</option>
                    <option>Khat</option>
                    <option>Dhan Peti</option>
                    <option>Peti</option>
                    <option>Vatilation</option>
                    <option>Door</option>
                    <option>Cupboard</option>
                    <option>Daicho</option>
                    <option>Bed Room Set</option>
                    <option>Cooler</option>
                    <option>Chair</option>
                    <option>Home</option>
                    <option>Materials</option>
                    <option>Shop</option>
                    <option>Gadi Bhara</option>
                    <option>Debts</option>
                    <option>Other</option>
                  </select>
                  <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                    <svg
                      class="fill-current h-4 w-4"
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 20 20"
                    >
                      <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
                    </svg>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Description
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="desc"
                  onChange={handleOnChange}
                  type="text"
                  placeholder="Detail"
                />
              </div>

              <div class="md:w-2/3">
                <button
                  class="shadow bg-blue-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 mx-5 px-4 rounded"
                  type="submit"
                >
                  Add Entery
                </button>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};
